import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'


import Vue from 'vue'
import helper from "common/helper.js"
import uView from '@/uni_modules/uview-ui'
import store from '@/store/index.js'
import ImageComponent from "imagecomponents/imageComponent.vue"
// console.log(store)
Vue.component("image-compt",ImageComponent)

Vue.config.productionTip = false
Vue.use(uView)
Object.keys(helper).forEach(v => {
	Vue.prototype['$' + v] = helper[v]
})

uni.getSystemInfo({
	success: (res) => {
		console.log('getSystemInfo',res)
		Vue.prototype.$isIos=res.osName=='ios'
	}
})
Vue.prototype.$wxLogin = function() {
	return new Promise((resolve, reject) => {
		uni.getUserProfile({
			desc: "wx",
			success: getUserProfileRes => {
				// console.log("getUserProfileRes", getUserProfileRes)
				uni.showModal({
					title: "微信授权登录",
					content: "是否同意微信授权登录",
					confirmText: "同意",
					cancelText: "拒绝",
					success: res => {
						if (res.confirm) {
							console.log('用户点击确定');
							uni.login({
								provider: 'weixin',
								success: function(loginRes) {
									let data = {
										...getUserProfileRes,
										...loginRes
									}
									// console.log("data", data)

									resolve(data)
								}
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
			}
		})
	})
}


App.mpType = 'app'
const app = new Vue({
	...App,
	store: store
})
app.$mount()